body {
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
  color: #a5a5a5;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}

ul {
  list-style: none;
}

.logo {
  color: #08A6E4;
  font-family: "Raleway", sans-serif;
  font-size: 2rem;
}

.btn {
  border-radius: 40px;
  background-color: #08A6E4;
  border: none;
  box-shadow: 5px 10px 14px rgba(7, 172, 230, 0.16);
  padding: 22px 80px;
}
@media (min-width: 576px) {
  .btn {
    padding: 22px 125px;
  }
}
.btn a {
  color: #fff;
}

h2 {
  color: #000;
  font-size: 30px;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
}

h3 {
  color: #000;
  font-size: 20px;
  line-height: 24px;
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  letter-spacing: 5.96px;
}

header {
  width: 100%;
  height: 70px;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
}

.logo {
  padding-left: 25px;
}

.menu {
  padding-right: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
}
.menu__list {
  display: flex;
}
@media (max-width: 576px) {
  .menu__list {
    position: absolute;
    left: 0;
    top: 95px;
    flex-direction: column;
    background-color: #fff;
    width: 100%;
    padding-bottom: 20px;
    display: none;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    z-index: 5555;
  }
  .menu__list li {
    display: block;
    padding: 15px 20px;
  }
}
.menu__btn {
  display: none;
  position: absolute;
  opacity: 0;
  top: -9999999px;
  left: -9999999px;
}
@media (max-width: 576px) {
  .menu__btn:checked ~ .menu__list {
    display: flex;
  }
  .menu__btn:checked ~ .menu__toggle span:nth-child(1) {
    transform: rotate(45deg) translate(2px, 5px);
  }
  .menu__btn:checked ~ .menu__toggle span:nth-child(2) {
    opacity: 0;
  }
  .menu__btn:checked ~ .menu__toggle span:nth-child(3) {
    transform: rotate(-45deg) translate(2px, -5px);
  }
}
.menu__toggle span {
  width: 20px;
  height: 2px;
  display: block;
  margin-bottom: 3px;
  background-color: #08A6E4;
  transition: 0.3s all ease-in-out;
}
.menu__toggle span:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .menu__toggle {
    display: none;
  }
}

.menu__list a {
  padding: 22px 16px;
  text-transform: uppercase;
  border-bottom: 3px solid transparent;
  margin-right: 20px;
}
@media (min-width: 1200px) {
  .menu__list a {
    padding-right: 100px;
  }
}
.menu__list a:last-child {
  margin-right: 0;
}
.menu__list a:hover {
  color: #000;
}
@media (min-width: 768px) {
  .menu__list a:hover:hover {
    padding: 22px 16px;
    border-color: #08A6E4;
  }
}
@media (min-width: 1200px) {
  .menu__list a:hover:hover {
    margin: 22px 84px 22px 0px;
  }
}

.jumbotron {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.content {
  text-align: center;
}
.content h1 {
  margin-top: 170px;
  font-size: 35px;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  line-height: 41px;
}
.content p {
  margin-top: 20px;
  line-height: 19px;
}
.content button {
  margin-top: 30px;
}

.image__container {
  max-width: 100%;
  height: 40vh;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 767px) {
  .image__container {
    margin-top: 25px;
    background-position: center;
    background-image: url(../../images/Background.png);
  }
}
@media (min-width: 768px) {
  .image__container {
    margin-top: 50px;
    background-image: url(../../images/Background@2x.png);
    background-position: center;
  }
}

.about {
  width: 100%;
  text-align: center;
}
.about h2 {
  margin-top: 50px;
  margin-bottom: 28px;
  padding: auto 36px;
}
.about__container {
  margin-bottom: 25px;
}

.about__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.about__container h3 {
  margin-top: 25px;
}
.about__container p {
  color: #a5a5a5;
  font-size: 17px;
  margin-top: 23px;
}

.icon {
  position: relative;
  width: 63px;
  height: 63px;
  left: 7%;
  top: 0;
}
@media (min-width: 420px) {
  .icon {
    left: 4%;
  }
}
.icon .eye {
  top: 13%;
  position: relative;
}

.icon::before {
  content: "";
  background: #fb3b64;
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 50px;
  z-index: -555;
  top: -10%;
  left: -35%;
}

.icon__eye::before {
  background: #08A6E4;
}

.icon__catalog::before {
  background: #55dfb4;
}